import { register, init, waitLocale } from "svelte-i18n";
const defaultLocale = "en";
register("en", () => import("../../chunks/en.js"));
register("nl", () => import("../../chunks/nl.js"));
init({
  fallbackLocale: defaultLocale,
  initialLocale: defaultLocale
});
const ssr = false;
const load = async () => {
  await waitLocale();
};
export {
  load,
  ssr
};
